<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {goodDetail,getComment,addToCart,addCollection,delMyCollection} from '../../service/api'
    import headTop from '../../components/header/headTop';

    export default {
        name: 'goodDetail',
        data(){
            return{
                test:'http://www.sixmol.com/uploads/uploads_message/20191105/0929a2e3a24ebd353a2695a3a197cbf3.png',
                goodId:'',
                specifications_id:'',
                currentTab:'details',
                info:[],
                evaluate:[],
                num: 1
            }
        },
        components:{
            headTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();

        },
        computed:{
            ...mapState([
                'userInfo',
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo','initCart'
            ]),
            async initData(){
                this.specifications_id = this.$route.query.specifications_id;
                let goodRes = await goodDetail(this.specifications_id);
                this.goodId = goodRes.goods_id;
                this.info = goodRes;
                console.log(goodRes);
                let evaluateRes = await getComment(
                    this.info.spec_id,
                    '',
                    ''
                );
                this.evaluate = evaluateRes;
                console.log(evaluateRes);
            },
            toggleTab:function(tab){
                this.currentTab = tab;
                console.log(tab);
            },
            handleChange(value) {//计数器
            },
            async addCart(){
                let res = await addToCart(this.info.goods_id, this.info.spec_id, this.num);
                if(res){
                    this.$message({
                        message: res.msg,
                        type: res.status==12000?'success':'warning'
                    });
                    this.initCart();
                }
                //goodsId,speId,number,goodSpeId
            },
            async toOrder(){
                if (this.userInfo && this.userInfo.user_id){
                    this.$router.push({path:"/order/confirm",query:{goodsId:this.goodId,speId:this.info.spec_id,number:this.num,type:1}});
                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            async toCollect(){
                if(this.info.isCollect){
                    //已经收藏过
                    this.$confirm('你确定要取消该收藏吗?', '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(async ()=> {
                        let res = await delMyCollection(this.info.collectId);
                        if(res.status == 12000){
                            this.$message({
                                message:res.msg,
                                type:'success'
                            });
                            this.initData();
                        }else{
                            this.$message({
                                message:res.msg,
                                type:'warning'
                            });
                        }
                    }).catch(() => {

                    });

                }else{
                    //goods_id,specifications_id,rows,page
                    var resAdd = await addCollection(
                        this.info.goods_id,
                        this.info.spec_id
                    );
                    if(resAdd){
                        this.$message({
                            message: resAdd.msg,
                            type: resAdd.status==12000?'success':'warning'
                        });
                        this.initData();
                    }
                }


            }
        },
        created(){

        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id && !this.list) {
                    this.initData();
                }
            }

        }
    }

</script>

