    /*评分选择stars choice*/
    .star_evaluate {
        position: relative;
        width: 100px;
        height: 20px;
        background: url("../../style/icon-star-default.png") repeat-x;
        background-size: 20px 20px;
        overflow: hidden;
        margin:0 auto;
    }
    .star,.score{display: block;height: 20px;width: 20px;position: absolute;}
    .star{z-index: 2;}
    .score{opacity: 0;}
    .star_1, .score_1 {left: 0;}
    .star_2, .score_2 {left: 20px;}
    .star_3, .score_3 {left: 40px;}
    .star_4, .score_4 {left: 60px;}
    .star_5, .score_5 {left: 80px;}
    /*鼠标悬浮*/
    .star:hover {
        cursor: pointer;
        background: url("../../style/icon-star-active.png") repeat-x;
