        left: 0;
        z-index: 1;
    }
    .starShow .star:hover{
        background: url("../../style/icon-star-default.png") repeat-x;
        background-size: 20px 20px;
        left: 0;
        z-index: 1;
        cursor: default;
    }
    .star_1:hover {width: 20px;}
    .star_2:hover {width: 40px;}
    .star_3:hover {width: 60px;}
    .star_4:hover {width: 80px;}
    .star_5:hover {width: 100px;}

    /*选中之后*/
    .score:checked + .star {background: url("../../style/icon-star-active.png") repeat-x;background-size: 20px 20px;left: 0;}
    .score_1:checked + .star_1 {width: 20px;}
    .score_2:checked + .star_2 {width: 40px;}
